html {
  min-height: 80%;
  position: relative;
}

.footer {
  width: 100%;
  height: 50px;
  background-color: #13afc9;
  display: flex;
  flex-direction: row-reverse;
  color: white;
  font-family: Patrick Hand SC;
  letter-spacing: 0.6px;
  position: fixed;
  bottom: 0;
  left: 0;
  &_text {
    margin-right: 20px;
  }
}
